import { useTheme } from '@emotion/react';
import { DIFFICULTY_FLASHCARD } from 'data/config';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { pxToRem } from 'util/util';

import { LinearProgress, Stack, Typography } from '@mui/material';

import FunTypo from '../ui/FunTypo';

const GameProgress = (props) => {
    const theme = useTheme();
    const { t } = useTranslation();

    const { game } = props;
    const phase = game.currentPhase;

    return (
        <Stack
            display="flex"
            alignItems="center"
            direction="column"
            justifyContent="space-between"
            sx={{
                margin: `${pxToRem(16)} 0`,
                width: '100%',
            }}
        >
            <Stack
                display="flex"
                alignItems="center"
                direction="row"
                justifyContent="space-between"
                sx={{
                    margin: `${pxToRem(4)} 0`,
                    width: '100%',
                }}
            >
                <Stack
                    display="flex"
                    alignItems="center"
                    direction="row"
                    sx={{
                        width: 'auto',
                    }}
                >
                    <Typography fontSize={pxToRem(18)} textAlign="right" fontWeight="700">
                        {t('Question')}: {game.currentTurn + 1 + ' / ' + game.questions.length}
                    </Typography>
                </Stack>
                {game.difficultyLevel !== DIFFICULTY_FLASHCARD && (
                    <Stack
                        display="flex"
                        alignItems="center"
                        direction="row"
                        sx={{
                            width: 'auto',
                        }}
                    >
                        <Typography fontSize={pxToRem(18)} textAlign="right" fontWeight="700">
                            {t('Score')}:
                        </Typography>
                        &nbsp;
                        <FunTypo
                            text={game.currentScore}
                            color={theme.palette.text.main.replace('#', '')}
                            stroke={false}
                            distance={pxToRem(2)}
                            flexGrow={0}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(20),
                                lineHeight: pxToRem(24),
                                fontWeight: 800,
                                width: 'auto',
                            }}
                        />
                    </Stack>
                )}
            </Stack>
            <LinearProgress
                color="success"
                variant="buffer"
                value={((game.currentTurn + 1) / game.questions.length) * 100}
                valueBuffer={((game.currentTurn + (phase === 2 ? 1 : 0)) / game.questions.length) * 100}
                sx={{
                    flexGrow: 1,
                    marginRight: pxToRem(16),
                    marginLeft: pxToRem(16),
                    width: '100%',
                }}
            />
        </Stack>
    );
};

export default GameProgress;
