import { useTheme } from '@emotion/react';
import { DIFFICULTY_FLASHCARD, difficultyLevels } from 'data/config';
import regionsData from 'data/regions.json';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { reStartGame } from 'store/actions';
import { useGameMode } from 'store/selector';
import { pxToRem } from 'util/util';

import { Stack, Typography } from '@mui/material';

import FunTypo from '../ui/FunTypo';
import MainButton from '../ui/MainButton';

const GameEnd = (props) => {
    const dispatch = useDispatch();
    const theme = useTheme();
    const { t, i18n } = useTranslation();
    const gameMode = useGameMode();
    const { game } = props;
    const region = regionsData.find((r) => r.eng === game.region);

    const handleClickPlayAgain = () => {
        dispatch(reStartGame());
    };

    return (
        <Stack
            display="flex"
            alignItems="center"
            direction="column"
            justifyContent="center"
            sx={{
                margin: `${pxToRem(16)} 0`,
                flexGrow: 1,
                width: '100%',
            }}
        >
            <Typography
                component="span"
                display="block"
                color="primary"
                fontSize={pxToRem(32)}
                lineHeight={pxToRem(40)}
                fontWeight={800}
                textAlign="center"
                mb={-1}
            >
                {gameMode.shortName[i18n.language]} {/*region?.article[i18n.language]*/}
            </Typography>
            <Typography
                component="span"
                display="block"
                color="secondary"
                fontSize={pxToRem(32)}
                lineHeight={pxToRem(40)}
                fontWeight={800}
                textAlign="center"
                mb={-1}
            >
                {region ? region[i18n.language] : t('World')}
            </Typography>
            <Typography
                component="span"
                display="block"
                color="success.main"
                fontSize={pxToRem(32)}
                lineHeight={pxToRem(40)}
                fontWeight={800}
                mb={2}
            >
                {t(difficultyLevels.find((d) => d.key === game.difficultyLevel).name)}
            </Typography>

            {game.difficultyLevel !== DIFFICULTY_FLASHCARD && (
                <>
                    <Typography display="block">
                        <FunTypo
                            text={t('Final_Score_1')}
                            color={theme.palette.text.title.replace('#', '')}
                            stroke={true}
                            strokeWidth={pxToRem(2)}
                            distance={pxToRem(5)}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(64),
                                lineHeight: pxToRem(72),
                                fontWeight: 800,
                                textAlign: 'center',
                                mb: pxToRem(-32),
                                transition: '0.25s ease',
                            }}
                        />
                        <FunTypo
                            text={t('Final_Score_2')}
                            color={theme.palette.text.title.replace('#', '')}
                            stroke={false}
                            strokeWidth={pxToRem(2)}
                            distance={pxToRem(6)}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(72),
                                lineHeight: pxToRem(84),
                                fontWeight: 800,
                                textAlign: 'center',
                                transition: '0.25s ease',
                            }}
                        />
                    </Typography>
                    <Stack
                        display="flex"
                        justifyContent="center"
                        alignItems="flex-end"
                        direction="row"
                        sx={{
                            width: 'auto',
                            paddingBottom: pxToRem(16),
                        }}
                    >
                        <FunTypo
                            text={game.currentScore}
                            color={theme.palette.primary.main.replace('#', '')}
                            stroke={false}
                            distance={pxToRem(7)}
                            flexGrow={0}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(96),
                                lineHeight: pxToRem(108),
                                fontWeight: 800,
                                width: 'auto',
                            }}
                        />
                        <FunTypo
                            text={'/' + game.questions.length}
                            color={theme.palette.success.main.replace('#', '')}
                            stroke={false}
                            distance={pxToRem(7)}
                            flexGrow={0}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(72),
                                lineHeight: pxToRem(84),
                                fontWeight: 800,
                                width: 'auto',
                            }}
                        />
                    </Stack>
                </>
            )}
            <MainButton
                buttonP={{
                    color: 'primary',
                    size: 'large',
                    onClick: handleClickPlayAgain,
                }}
            >
                {t('Play Again')}
            </MainButton>
        </Stack>
    );
};

export default GameEnd;
