import { useTheme } from '@emotion/react';
import {
    CAPITAL,
    COUNTRY_BY_MAP,
    DIFFICULTY_FLASHCARD,
    DIFFICULTY_NORMAL,
    FLAG,
    FULL,
    PEAK,
    SHORT,
    TRIVIA,
    difficultyLevels,
    gameModes,
} from 'data/config';
import regionsData from 'data/regions.json';
import * as React from 'react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { startGame } from 'store/actions';
import { useGameOptions, useShowAdvancedOptions } from 'store/selector';
import { Spacer, getGameSVG, pxToRem } from 'util/util';

import {
    Box,
    Button,
    Card,
    Grow,
    InputLabel,
    Stack,
    Switch,
    TextField,
    ToggleButton,
    ToggleButtonGroup,
    Typography,
    alpha,
} from '@mui/material';

import MainButton from './ui/MainButton';
import OptionRow from './ui/OptionRow';

const NewGameOptions = (props) => {
    const { handleClickChangeMode, gameMode, gameSubMode: _gameSubMode } = props;

    const dispatch = useDispatch();
    const theme = useTheme();
    const { t, i18n } = useTranslation();

    const gameOptions = useGameOptions();
    const showAdvancedOptions = useShowAdvancedOptions();

    const [independantOnly, setIndependantOnly] = useState((gameOptions && gameOptions.independantOnly) ?? true);
    const [gameSubMode, setSubGameMode] = useState(_gameSubMode);
    const [gameLength, setGameLength] = useState((gameOptions && gameOptions.gameLength) || SHORT);
    const [region, setRegion] = useState((gameOptions && gameOptions.region) || 'all');
    const [difficultyLevel, setDifficultyLevel] = useState(
        (gameOptions &&
            (gameOptions.difficultyLevel === DIFFICULTY_FLASHCARD && gameMode === TRIVIA
                ? DIFFICULTY_NORMAL
                : gameOptions.difficultyLevel)) ??
            DIFFICULTY_NORMAL
    );
    const [hideBackgroundMap, setHideBackgroundMap] = useState((gameOptions && gameOptions.hideBackgroundMap) ?? false);

    const handleChangeSubMode = (event) => {
        setSubGameMode(event.target.value);
    };

    const handleChangeLength = (event) => {
        setGameLength(event.target.value);
    };

    const handleChangeIndependant = (event) => {
        setIndependantOnly(event.target.checked);
    };

    const handleChangeRegion = (event) => {
        setRegion(event.target.value);
    };

    const handleChangeDifficultyLevel = (event) => {
        setDifficultyLevel(event.target.value);
    };

    const handleClickHideBackgroundMap = (event) => {
        setHideBackgroundMap(event.target.checked);
    };

    const handleClickStart = () => {
        dispatch(startGame(gameSubMode, region, difficultyLevel, independantOnly, gameLength, hideBackgroundMap));
    };

    const optionFlashcard = difficultyLevels.find((option) => option.key === DIFFICULTY_FLASHCARD);

    return (
        <Grow in={true}>
            <Card
                aria-label={t('Options')}
                sx={{
                    textAlign: 'center',
                    minWidth: pxToRem(240),
                    position: 'relative',
                }}
            >
                <Stack spacing={1.5}>
                    <Button
                        variant="outlined"
                        size="medium"
                        py={pxToRem(12)}
                        sx={{
                            svg: {
                                color: theme.palette.secondary.contrast,
                                height: pxToRem(36),
                                width: pxToRem(36),
                                fill: 'currentColor',
                                mr: pxToRem(12),
                                filter: `drop-shadow(2px 2px 0px ${alpha(theme.palette.shadow, 0.15)})`,
                            },
                            mb: pxToRem(6),
                        }}
                        onClick={handleClickChangeMode}
                    >
                        {getGameSVG(gameMode)}
                        <Typography
                            variant="h2"
                            display="block"
                            color="secondary.contrast"
                            fontSize={pxToRem(24)}
                            lineHeight={pxToRem(40)}
                            mr={pxToRem(12)}
                            sx={{
                                textShadow: `2px 2px 0px ${alpha(theme.palette.shadow, 0.1)}`,
                            }}
                        >
                            {gameModes.find((m) => m.key === gameMode).name[i18n.language]}
                        </Typography>
                    </Button>
                    <Spacer height={1} />
                    {(gameMode === FLAG || gameMode === CAPITAL || gameMode === PEAK) && (
                        <OptionRow>
                            <InputLabel htmlFor="Mode">{t('Variant')}:</InputLabel>
                            <ToggleButtonGroup
                                id="Mode"
                                name="Mode"
                                color="secondary"
                                value={gameSubMode}
                                exclusive
                                onChange={handleChangeSubMode}
                                size="small"
                                sx={{
                                    justifyContent: 'flex-end',
                                }}
                            >
                                <ToggleButton key={gameMode} value={gameMode}>
                                    {gameModes.find((m) => m.key === gameMode)?.subModeLabel[i18n.language]}
                                </ToggleButton>
                                <ToggleButton key={`country_${gameMode}`} value={`country_${gameMode}`}>
                                    {gameModes.find((m) => m.key === `country_${gameMode}`).subModeLabel[i18n.language]}
                                </ToggleButton>
                            </ToggleButtonGroup>
                        </OptionRow>
                    )}
                    {gameMode !== TRIVIA && (
                        <OptionRow>
                            <InputLabel htmlFor="Region">{t('Region')}:</InputLabel>
                            <TextField
                                id="Region"
                                name="Region"
                                disabled={gameMode === TRIVIA}
                                onChange={handleChangeRegion}
                                required
                                select
                                size="small"
                                SelectProps={{ native: true }}
                                value={region}
                                sx={{
                                    mt: 0,
                                }}
                            >
                                <option key="all" value="all">
                                    {t('World')}
                                </option>
                                {regionsData.map((option) =>
                                    option.eng === 'Antarctic' ? null : (
                                        <option key={option.eng} value={option.eng}>
                                            {option[i18n.language]}
                                        </option>
                                    )
                                )}
                            </TextField>
                        </OptionRow>
                    )}
                    <OptionRow>
                        <InputLabel htmlFor="Length">{t('Length')}:</InputLabel>
                        <ToggleButtonGroup
                            id="Length"
                            name="Length"
                            color="secondary"
                            value={gameLength}
                            exclusive
                            onChange={handleChangeLength}
                            size="small"
                            sx={{
                                justifyContent: 'flex-end',
                            }}
                        >
                            <ToggleButton value={SHORT} size="small">
                                {t('Short')}
                            </ToggleButton>
                            <ToggleButton value={FULL} size="small">
                                {t('Full')}
                            </ToggleButton>
                        </ToggleButtonGroup>
                    </OptionRow>
                    <OptionRow>
                        <InputLabel htmlFor="Difficulty">{t('Difficulty')}:</InputLabel>
                        <ToggleButtonGroup
                            id="Difficulty"
                            name="Difficulty"
                            color="secondary"
                            value={difficultyLevel}
                            exclusive
                            onChange={handleChangeDifficultyLevel}
                            aria-label={t('Difficulty')}
                            size="small"
                            sx={{
                                justifyContent: 'flex-end',
                                flexWrap: 'wrap',
                                maxWidth: '200px',
                            }}
                        >
                            {difficultyLevels
                                .filter((option) => option.key !== DIFFICULTY_FLASHCARD)
                                .map((option) => (
                                    <ToggleButton key={option.key} value={option.key} disabled={option.disabled}>
                                        {t(option.name)}
                                    </ToggleButton>
                                ))}
                            {showAdvancedOptions && gameMode !== TRIVIA && (
                                <ToggleButton
                                    key={optionFlashcard.key}
                                    value={optionFlashcard.key}
                                    disabled={optionFlashcard.disabled}
                                >
                                    {t(optionFlashcard.name)}
                                </ToggleButton>
                            )}
                        </ToggleButtonGroup>
                    </OptionRow>
                    {showAdvancedOptions && gameMode !== TRIVIA && (
                        <OptionRow>
                            <InputLabel htmlFor="Independent">{t('Independent countries only')}:</InputLabel>
                            <Switch
                                id="Independent"
                                name="Independent"
                                checked={independantOnly}
                                onChange={handleChangeIndependant}
                                sx={{
                                    mt: pxToRem(10),
                                    mr: 0,
                                    mb: 0,
                                    ml: pxToRem(12),
                                }}
                            />
                        </OptionRow>
                    )}
                    {showAdvancedOptions && (gameMode === TRIVIA || gameMode === COUNTRY_BY_MAP) && (
                        <OptionRow>
                            <InputLabel htmlFor="HideBackgroundMap">{t('Hide map background')}:</InputLabel>
                            <Switch
                                id="HideBackgroundMap"
                                name="HideBackgroundMap"
                                checked={hideBackgroundMap}
                                onChange={handleClickHideBackgroundMap}
                                sx={{
                                    mt: 0,
                                    mr: 0,
                                    mb: 0,
                                    ml: pxToRem(12),
                                }}
                            />
                        </OptionRow>
                    )}
                    <Spacer height={1} />
                    <MainButton
                        buttonP={{
                            color: 'primary',
                            size: 'large',
                            onClick: handleClickStart,
                            mt: pxToRem(16),
                        }}
                    >
                        {t('Start Game')}
                    </MainButton>
                </Stack>
            </Card>
        </Grow>
    );
};

export default NewGameOptions;
