import { useTheme } from '@emotion/react';
import GameMap from 'components/ui/GameMap';
import { COUNTRY_BY_FLAG, COUNTRY_BY_MAP, DIFFICULTY_EXPERT, DIFFICULTY_FLASHCARD, questionTypes } from 'data/config';
import * as React from 'react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { gameAnswer } from 'store/actions';
import { getGameSVG, pxToRem } from 'util/util';

// import { useGameMode } from 'store/selector';
import { Box, Card, Grow, Stack, Typography } from '@mui/material';

import GameFlag from '../ui/GameFlag';
import GameButton from './GameButton';

const GameQuestion = (props) => {
    const { game } = props;

    const dispatch = useDispatch();
    const { i18n } = useTranslation();
    const theme = useTheme();

    const [chosenAnswer, setChosenAnswer] = useState('');

    const turn = game.currentTurn;
    const question = game.questions && game.questions[turn];
    const difficultyLevel = game.difficultyLevel;
    const rightAnswer = question ? question.answer : undefined;
    const questionType = questionTypes.find((qt) => qt.key === question.questionTypeKey);

    // preload images for next Question
    const nextQuestionImages = () => {
        const nextQuestion = game.questions && game.questions.length >= turn && game.questions[turn + 1];
        const nextQuestionKey = nextQuestion?.questionTypeKey;
        if (nextQuestionKey === 'flag' || nextQuestionKey === 'country_flag') {
            const countries =
                nextQuestionKey === 'flag'
                    ? nextQuestion.choices.map((choice) => choice.toLowerCase())
                    : [nextQuestion.country.toLowerCase()];
            countries.forEach((c) => {
                var img = new Image();
                img.src = `${process.env.PUBLIC_URL}/assets/flagsAndGeo/svg/${c.toLowerCase()}.svg`;
            });
        }
        return null;
    };

    const handleChoiceClick = (_chosenAnswer) => {
        if (phase === 1) {
            setChosenAnswer(_chosenAnswer);
            dispatch(gameAnswer(_chosenAnswer));
        }
    };

    const phase = game.currentPhase;

    const textReplace = (text, replacements) => {
        if (!replacements || replacements.length === 0) {
            return text;
        }
        replacements.forEach((rep, i) => {
            text = text.split('{' + i + '}').join(rep);
        });
        return text;
    };

    const questionPhrase = (
        <>
            <Typography
                fontSize={pxToRem(20)}
                fontWeight="600"
                lineHeight={pxToRem(24)}
                mb={questionType.questionPhraseInverted ? 0 : pxToRem(16)}
                mt={questionType.questionPhraseInverted ? pxToRem(16) : 0}
            >
                {textReplace(questionType.questionPhrase[i18n.language], question.questionPhraseValues)}
            </Typography>
            {questionType.questionSubPhrase && (
                <Typography fontSize={pxToRem(12)} fontWeight="400" lineHeight={pxToRem(12)} mt={pxToRem(12)} mb="0">
                    {questionType.questionSubPhrase[i18n.language]}
                </Typography>
            )}
        </>
    );

    const choiceButton = (choice, index, isFlag) => {
        return (
            <Grow in={phase > 0} timeout={index * 75 + 200} key={question.country + index} easing="linear">
                <Box width={isFlag ? `calc(50% - 12px)` : '100%'} minHeight={isFlag ? '126px' : 'auto'}>
                    <GameButton
                        onClick={() => {
                            handleChoiceClick(choice);
                        }}
                        colorEffect={theme.palette.primary.main.replace('#', '')}
                        key={choice + index}
                        phase={phase}
                        choice={choice}
                        rightAnswer={rightAnswer}
                        chosenAnswer={chosenAnswer}
                        isFlag={isFlag}
                        answerAdditionnalText={question.answerAdditionnalText}
                    >
                        {question.questionTypeKey === 'flag' ? (
                            <GameFlag
                                country={choice}
                                svgOverrides={{
                                    width: '90%',
                                    maxWidth: pxToRem(180),
                                    maxHeight: pxToRem(100),
                                    filter: 'drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.3))',
                                }}
                            />
                        ) : (
                            <>
                                {difficultyLevel === DIFFICULTY_EXPERT && phase < 2
                                    ? choice[0] + ' * * * ' + choice[choice.length - 1]
                                    : difficultyLevel === DIFFICULTY_FLASHCARD && phase < 2
                                      ? ' * * * '
                                      : choice}
                            </>
                        )}
                    </GameButton>
                </Box>
            </Grow>
        );
    };

    const choiceButtons = (
        <>
            {question && difficultyLevel === DIFFICULTY_FLASHCARD
                ? choiceButton(question.answer, 1, false)
                : question && question.choices
                  ? question.choices.map((choice, index) =>
                        choiceButton(choice, index, question.questionTypeKey === 'flag')
                    )
                  : null}
        </>
    );

    return (
        <Stack
            alignItems="center"
            flexDirection="column"
            justifyContent="center"
            spacing={2}
            sx={{
                flexGrow: 1,
                width: '100%',
                position: 'relative',
            }}
        >
            <Grow in={phase > 0} timeout={200} key={question.country}>
                <Card
                    sx={{
                        px: 2,
                        pt: question.questionTypeKey === 'country_map' ? 3 : 4,
                        pb: question.questionTypeKey === 'country_map' ? 2 : 4,
                        mt: 1,
                        textAlign: 'center',
                        width: '100%',
                        boxSizing: 'border-box',
                        position: 'relative',
                    }}
                >
                    {question.questionTypeKey !== COUNTRY_BY_MAP && (
                        <Box
                            sx={{
                                display: 'block',
                                position: 'absolute',
                                top: 0,
                                left: 0,
                                zIndex: 0,
                                width: '100%',
                                height: '100%',
                                right: 0,
                                svg: {
                                    color: theme.palette.neutral[200],
                                    height: pxToRem(160),
                                    width: pxToRem(160),
                                    fill: 'currentColor',
                                    position: 'absolute',
                                    // opacity: 0.1,
                                    left: pxToRem(10),
                                    ...(question.questionTypeKey === COUNTRY_BY_FLAG
                                        ? { bottom: pxToRem(-5) }
                                        : { bottom: pxToRem(-30) }),
                                    transform: 'rotate(-10deg)',
                                    opacity: 0.5,
                                },
                            }}
                        >
                            {getGameSVG(question.questionTypeKey)}
                        </Box>
                    )}
                    <Box
                        sx={{
                            zIndex: 1,
                            position: 'relative',
                        }}
                    >
                        {!questionType.questionPhraseInverted && <>{questionPhrase}</>}
                        {question.questionTypeKey === 'country_flag' ? (
                            <React.Fragment>
                                <GameFlag
                                    country={question.country}
                                    svgOverrides={{
                                        width: '90%',
                                        maxWidth: pxToRem(180),
                                        maxHeight: pxToRem(100),
                                        filter: 'drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.3))',
                                    }}
                                />
                            </React.Fragment>
                        ) : question.questionTypeKey === 'country_map' ? (
                            <GameMap country={question.country} />
                        ) : (
                            <Typography
                                color="success.main"
                                fontSize={pxToRem(24)}
                                fontWeight="800"
                                lineHeight={pxToRem(28)}
                            >
                                {question ? question.question /*+(question.flag ? " "+question.flag : '')*/ : ''}
                            </Typography>
                        )}
                        {questionType.questionPhraseInverted && <>{questionPhrase}</>}
                    </Box>
                </Card>
            </Grow>
            {question.questionTypeKey === 'flag' ? (
                <Stack
                    direction="row"
                    flexWrap="wrap"
                    spacing={2}
                    useFlexGap
                    sx={{
                        width: '100%',
                    }}
                >
                    {choiceButtons}
                </Stack>
            ) : (
                <>{choiceButtons}</>
            )}
            {nextQuestionImages()}
        </Stack>
    );
};

export default GameQuestion;
