import { ReactComponent as Brain } from 'assets/images/brain.svg';
import { ReactComponent as Flag } from 'assets/images/flag1.svg';
import { ReactComponent as Location } from 'assets/images/location.svg';
import { ReactComponent as WorldMap } from 'assets/images/map.svg';
import { ReactComponent as Peak } from 'assets/images/peak.svg';
import styled from 'styled-components';

export const shuffle = (array) => {
    let currentIndex = array.length,
        randomIndex;

    // While there remain elements to shuffle.
    while (currentIndex > 0) {
        // Pick a remaining element.
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
    }

    return array;
};

export const pxToRem = (px) => {
    return px / 14 + 'rem';
};

export const pxToRemInt = (px) => {
    return px / 14;
};

export const getGameSVG = (mode) => {
    let svg;
    switch (mode) {
        case 'capital':
        case 'country_capital':
        case 'is_capital':
        case 'not_capital':
            svg = <Location />;
            break;
        case 'country_map':
            svg = <WorldMap />;
            break;
        case 'flag':
        case 'country_flag':
            svg = <Flag />;
            break;
        case 'peak':
        case 'country_peak':
            svg = <Peak />;
            break;
        case 'trivia':
        default:
            svg = <Brain />;
            break;
    }
    return svg;
};

const StyledSpacer = styled.div`
    display: block;
    height: ${(props) => pxToRem(props.height)};
`;

export const Spacer = (props) => {
    return <StyledSpacer height={props.height} />;
};
