import { useTheme } from '@emotion/react';
import { CAPITAL, gameModes, questionTypes } from 'data/config';
import useEmblaCarousel from 'embla-carousel-react';
import * as React from 'react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useGameOptions } from 'store/selector';
import { getGameSVG, pxToRem } from 'util/util';

import { Box, Button, Grow, Stack, Typography, alpha } from '@mui/material';

import NewGameOptions from './NewGameOptions';
import FunTypo from './ui/FunTypo';

const NewGame = (props) => {
    const { cssTransition } = props;
    const theme = useTheme();
    const { t, i18n } = useTranslation();
    const gameOptions = useGameOptions();

    const [step, setStep] = useState(0);
    const [gameMode, setGameMode] = useState((gameOptions && gameOptions.gameMode) || CAPITAL);
    const [gameSubMode, setSubGameMode] = useState((gameOptions && gameOptions.gameMode) || CAPITAL);

    const handleClickMode = (event, gameModeKey) => {
        setGameMode(gameModeKey);
        setSubGameMode(gameModeKey);
        setStep(1);
    };
    const handleClickChangeMode = (event) => {
        setStep(0);
    };

    const mainGameModes = gameModes.filter((option) => option.isMain);

    const OPTIONS = { dragFree: true, containScroll: false };
    const [emblaRef, emblaApi] = useEmblaCarousel(OPTIONS);
    if (emblaApi && mainGameModes) {
        const gameIndex = mainGameModes.findIndex((m) => m.key === gameMode);
        emblaApi.scrollTo(gameIndex, true);
    }
    return (
        <>
            <Stack
                alignItems="center"
                direction="column"
                justifyContent="center"
                maxWidth="100%"
                height="100%"
                flexGrow={1}
                gap={3}
                sx={{
                    ...cssTransition,
                    overflowX: 'hidden',
                }}
            >
                {step === 0 && (
                    <>
                        <Typography
                            variant="h2"
                            display="block"
                            width="100%"
                            fontSize={pxToRem(32)}
                            fontWeight="800"
                            textAlign="center"
                        >
                            <FunTypo
                                text={t('Game modes')}
                                color={theme.palette.text.main.replace('#', '')}
                                stroke={false}
                                strokeWidth={pxToRem(2)}
                                distance={pxToRem(3)}
                                sx={{
                                    fontSize: pxToRem(40),
                                    lineHeight: pxToRem(30),
                                    fontWeight: 800,
                                }}
                            />
                            {t('Game modes2') && (
                                <>
                                    <br />
                                    <FunTypo
                                        text={t('Game modes2')}
                                        color={theme.palette.text.main.replace('#', '')}
                                        stroke={false}
                                        strokeWidth={pxToRem(2)}
                                        distance={pxToRem(3)}
                                        sx={{
                                            fontSize: pxToRem(40),
                                            lineHeight: pxToRem(30),
                                            fontWeight: 800,
                                        }}
                                    />
                                </>
                            )}
                        </Typography>
                        <Box
                            ref={emblaRef}
                            sx={{
                                maxWidth: '100%',
                                padding: '32px',
                                my: '-32px',
                                maskImage:
                                    'linear-gradient(90deg, transparent 0%, black 5%, black 95%, transparent 100%)',
                            }}
                        >
                            <Box
                                sx={{
                                    display: 'flex',
                                    touchAction: 'pan-y pinch-zoom',
                                    marginLeft: 'calc(1rem * -1)',
                                }}
                            >
                                {mainGameModes.map((option, index) => {
                                    const questionType = questionTypes.find((qt) => option.questionType === qt.key);
                                    return (
                                        <Box
                                            key={option.key}
                                            sx={{
                                                transform: 'translate3d(0, 0, 0)',
                                                flex: '0 0 140px',
                                                minWidth: 0,
                                                pl: '1rem',
                                                overflowY: 'visible',
                                            }}
                                        >
                                            <Grow in={true} timeout={index * 100 + 200} key={option.key}>
                                                <Button
                                                    variant="mode"
                                                    size="large"
                                                    disabled={
                                                        questionType !== undefined &&
                                                        questionType.disabledOffline &&
                                                        !navigator.onLine
                                                    }
                                                    sx={{
                                                        width: '100%',
                                                        display: 'flex',
                                                        flexDirection: 'column',
                                                        alignItems: 'center',
                                                        py: pxToRem(32),
                                                        '&:hover': {
                                                            backgroundColor: theme.palette.background.tileHover,
                                                        },
                                                        svg: {
                                                            color: theme.palette.secondary.contrast,
                                                            height: pxToRem(64),
                                                            width: pxToRem(64),
                                                            fill: 'currentColor',
                                                            filter: `drop-shadow(2px 2px 0px ${alpha(theme.palette.shadow, 0.15)})`,
                                                        },
                                                    }}
                                                    onClick={(e) => handleClickMode(e, option.key)}
                                                >
                                                    {getGameSVG(option.key)}
                                                    <Typography
                                                        component="span"
                                                        fontSize={pxToRem(20)}
                                                        fontWeight="700"
                                                        mt={pxToRem(8)}
                                                        color="secondary.contrast"
                                                        sx={{
                                                            textShadow: `2px 2px 0px ${alpha(theme.palette.shadow, 0.1)}`,
                                                        }}
                                                    >
                                                        {option.name[i18n.language]}
                                                    </Typography>
                                                </Button>
                                            </Grow>
                                        </Box>
                                    );
                                })}
                            </Box>
                        </Box>
                    </>
                )}
                {step === 1 && (
                    <NewGameOptions
                        gameMode={gameMode}
                        gameSubMode={gameSubMode}
                        handleClickChangeMode={handleClickChangeMode}
                    />
                )}
            </Stack>
        </>
    );
};

export default NewGame;
