import countriesData from 'data/countries.json';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { pxToRem } from 'util/util';

import CloseIcon from '@mui/icons-material/Close';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import InstallDesktopIcon from '@mui/icons-material/InstallDesktop';
import InstallMobileIcon from '@mui/icons-material/InstallMobile';
import LightModeIcon from '@mui/icons-material/LightMode';
import {
    Dialog,
    DialogContent,
    DialogTitle,
    FormControlLabel,
    IconButton,
    InputLabel,
    Stack,
    Switch,
    ToggleButton,
    ToggleButtonGroup,
    Typography,
} from '@mui/material';

import { editSettings } from '../store/actions';
import { useIsDarkMode, useLanguage, useShowAdvancedOptions } from '../store/selector';
import MainButton from './ui/MainButton';
import OptionRow from './ui/OptionRow';

const cacheAllAssets = () => {
    // download assets
    countriesData.forEach((country) => {
        if (country && country.cca3) {
            var img = new Image();
            img.src = `${process.env.PUBLIC_URL}/assets/flagsAndGeo/svg/${country.cca3.toLowerCase()}.svg`;
        }
    });
};

const Settings = (props) => {
    const { settingsDialogOpen, handleClose, deferredPrompt } = props;
    const [showInstallButton, setShowInstallButton] = useState(true);
    const dispatch = useDispatch();
    const showAdvancedOptions = useShowAdvancedOptions();
    const isDarkMode = useIsDarkMode();
    const language = useLanguage();
    const { t, i18n } = useTranslation();

    const handleClickAdvancedOptions = (event) => {
        dispatch(editSettings({ showAdvancedOptions: event.target.checked }));
    };
    const handleClickLightMode = () => {
        dispatch(editSettings({ isDarkMode: !isDarkMode }));
    };
    const handleClickLanguage = (event) => {
        dispatch(editSettings({ language: event.target.value }));
        i18n.changeLanguage(event.target.value);
    };

    const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    // Installation must be done by a user gesture! Here, the button click
    const handleClickInstall = (e) => {
        // hide our user interface that shows our A2HS button
        // Show the prompt
        deferredPrompt.prompt();
        // Wait for the user to respond to the prompt
        deferredPrompt.userChoice.then((choiceResult) => {
            if (choiceResult.outcome === 'accepted') {
                // console.log('User accepted the A2HS prompt');
                setShowInstallButton(false);
                cacheAllAssets();
            } else {
                // console.log('User dismissed the A2HS prompt');
            }
        });
    };

    return (
        <Dialog open={settingsDialogOpen} onClose={handleClose} aria-labelledby="settings-dialog-title">
            <DialogTitle id="settings-dialog-title">{t('Settings')}</DialogTitle>
            <IconButton
                aria-label={t('Close')}
                onClick={handleClose}
                color="text"
                size="large"
                sx={{
                    position: 'absolute',
                    right: pxToRem(16),
                    top: pxToRem(20),
                }}
            >
                <CloseIcon />
            </IconButton>
            <DialogContent>
                <Stack justifyItems="center" spacing={3}>
                    <OptionRow>
                        <InputLabel htmlFor="Language">{t('Language')}:</InputLabel>
                        <ToggleButtonGroup
                            id="Language"
                            name="Language"
                            color="primary"
                            value={language}
                            exclusive
                            onChange={handleClickLanguage}
                            size="small"
                            variant="text"
                            sx={{
                                justifyContent: 'center',
                            }}
                        >
                            {Object.keys(i18n.store.data).map((lang) => (
                                <ToggleButton key={lang} value={lang}>
                                    {t(lang)}
                                </ToggleButton>
                            ))}
                        </ToggleButtonGroup>
                    </OptionRow>
                    <OptionRow>
                        <InputLabel htmlFor="AdvancedOptions">{t('Advanced game options')}:</InputLabel>
                        <Switch
                            id="AdvancedOptions"
                            name="AdvancedOptions"
                            checked={showAdvancedOptions}
                            onChange={handleClickAdvancedOptions}
                            sx={{
                                mt: 0,
                                mr: 0,
                                mb: 0,
                                ml: pxToRem(12),
                            }}
                        />
                    </OptionRow>
                    <OptionRow>
                        <InputLabel htmlFor="DarkMode">
                            {t('Dark Mode')} <DarkModeIcon sx={{ verticalAlign: 'bottom' }} />:
                        </InputLabel>
                        <Switch
                            id="DarkMode"
                            name="DarkMode"
                            checked={isDarkMode}
                            onChange={handleClickLightMode}
                            sx={{
                                mt: 0,
                                mr: 0,
                                mb: 0,
                                ml: pxToRem(12),
                            }}
                        />
                    </OptionRow>
                    {deferredPrompt && showInstallButton && (
                        <MainButton
                            buttonP={{
                                variant: 'outlined',
                                color: 'primary',
                                size: 'large',
                                onClick: handleClickInstall,
                                endIcon: isMobile ? <InstallMobileIcon /> : <InstallDesktopIcon />,
                            }}
                            typoP={{
                                fontSize: pxToRem(16),
                                fontWeight: '700',
                                lineHeight: pxToRem(28),
                            }}
                        >
                            {`${t('Install')} ${t('Country')} ${t('Quiz')}`}
                        </MainButton>
                    )}
                    <Typography component="p" fontSize={pxToRem(12)} textAlign="center">
                        {`${t('Country')} ${t('Quiz')}`} @2024 Joanie Lessnick
                    </Typography>
                </Stack>
            </DialogContent>
        </Dialog>
    );
};

export default Settings;
